import React from 'react';

const LoginForm = () => {
    return (
    <>
        <div className="login-form__contianer">
            <div className="login-form__inner">
                <h1 className="login-from__logo"><img class="login-form__logo-image" src="/static/logo.png"/></h1>
                <div className="login">
                    <form>
                        <h2 className="login__email-title">Email Login</h2>
                        <div className="member-input">
                            <div className="member-input__state">
                                <input class="member-input__box" type="text" placeholder="Email"/>
                            </div>
                            <div className="member-input__state">
                                <input class="member-input__box" type="text" placeholder="Password"/>
                            </div>
                            <button type="submit" class="member-button login__btn">
                                LOGIN
                            </button>
                        </div>
                    </form>
                </div>
            </div>
        </div>
        <style global jsx>
            {`
                .LoginForm {
                    width: 400px;
                    height: 600px;
                    margin: 0 auto;
                    top: 50%;
                    margin-top: 250px;
                }
                .modal-wrapper{
                    width: 400px;
                    height: 300px;
                    background-color: #2A2D33;
                    border-radius: 40px;
                    color: #515866;
                    box-shadow: 0 40px 80px rgba(0,0,0,0.5);
                }
                .inp_text {
                    padding-left: 20px;
                    width: 200px;
                    height: 50px;
                    margin: 0;
                    box-sizing: border-box;
                }
                .screen_out {
                    overflow: hidden;
                    line-height: 0;
                    text-indent: -9999px;
                    position: absolute;
                    width: 0;
                    height: 0;
                }
                .inp_text input {
                    width: 100%;
                    height:100%;
                    line-height: 1.5;
                    font-family: Noto Sans DemiLight,AppleSDGothicNeo-Regular,Malgun Gothic,맑은 고딕,dotum,돋움,sans-serif;
                }
            `}
        </style>
    </>
    )
}

export default LoginForm;